import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Grid, List, ListItem, ListItemText, Typography, Divider, Box, Button, CircularProgress, TextField } from '@mui/material';
import { styled } from '@mui/system';
import Cookies from 'js-cookie';

const Root = styled(Box)(({ theme }) => ({
    padding: theme.spacing(5),
    backgroundColor: '#f5f5f5',
    height: '100vh',
}));

const Sidebar = styled(Grid)({
    borderRight: '1px solid #ddd',
    padding: '20px',
});

const StyledListItem = styled(ListItem)(({ theme }) => ({
    '&.Mui-selected': {
        backgroundColor: '#e8f0fe',
    },
    '&:hover': {
        backgroundColor: '#e8f0fe',
    },
    padding: '20px',
}));

const EditButton = styled(Button)(({ theme }) => ({
    marginTop: theme.spacing(2),
    backgroundColor: '#3f51b5',
    color: '#fff',
    '&:hover': {
        backgroundColor: '#303f9f',
    },
}));

const StudentProfile = () => {
    const [selectedSection, setSelectedSection] = useState('Personal info');
    const [loading, setLoading] = useState(true);
    const [name, setName] = useState('');
    const [phone_number, setPhone_number] = useState('');
    const [isChangingPassword, setIsChangingPassword] = useState(false);
    const [oldPassword, setOldPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');

    const school = 'Indian Institute of Technology, Delhi';
    const nav=useNavigate();

    const [isEditing, setIsEditing] = useState(false);
    const handleChangePasswordClick = () => {
        setIsChangingPassword(true);
      };
    
    const handleSaveNewPasswordClick = async () => {
        setIsChangingPassword(false);
        setLoading(true);
        await fetch('http://localhost:3001/api/student/profile/changePassword', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'x-access-token': Cookies.get('token'),
            },
            body: JSON.stringify({
                kerberos: Cookies.get('kerberos'),
                oldPassword: oldPassword,
                newPassword: newPassword,
            }),
        }).then(response => {
            if (response.status === 401|| response.status === 403) {
                alert('Unauthorized');
                nav('../../')
            }
            else if(response.status === 400) {
                alert('Incorrect old password');
            }
            else {
                return response.json();
            }
        }).catch(err => {
            alert('Incorrect old password');
        });
        setOldPassword('');
        setNewPassword('');
        setLoading(false);
    
    };
    const handleEditClick = () => {
      setIsEditing(true);
    };
  
    const handleSaveClick = async () => {
        if (!(/^\d{10}$/.test(phone_number))) {
            alert('Invalid phone number');
            return;
        }
        setIsEditing(false);
        setLoading(true);
        await fetch('http://localhost:3001/api/student/profile/'+Cookies.get('kerberos'),{
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
                'x-access-token': Cookies.get('token'),
            },
            body: JSON.stringify({
                name: name,
                phone_number: phone_number,
            }),
        }).then(response => {
            if (response.status === 401|| response.status === 403) {
                alert('Unauthorized');
                nav('../../')
            } else {
                return response.json();
            }
        }
        ).then(data => {
            setName(data.name);
            setPhone_number(data.phone_number);
            setLoading(false);
        });
    };

    useEffect(() => {
        fetch('http://localhost:3001/api/student/profile/'+Cookies.get('kerberos'),{
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'x-access-token': Cookies.get('token'),
            },
        }).then(response => {
            if (response.status === 401|| response.status === 403) {
                alert('Unauthorized');
                nav('../../')
            } else {
                return response.json();
            }
        }).then(data => {
            setName(data.name);
            setPhone_number(data.phone_number);
            setLoading(false);
        });
    }, []);
    return (
        <>
        {loading ? <CircularProgress /> : 
        <Root>
            <Grid container>
                <Sidebar item xs={3}>
                    <List component="nav">
                        <StyledListItem button selected={selectedSection === 'Personal info'} onClick={() => setSelectedSection('Personal info')}>
                            <ListItemText primary="Personal info" />
                        </StyledListItem>
                        <StyledListItem button selected={selectedSection === 'Security'} onClick={() => setSelectedSection('Security')}>
                            <ListItemText primary="Security" />
                        </StyledListItem>
                    </List>
                </Sidebar>
                <Grid item xs={9} style={{ padding: '20px' }}>
                {selectedSection === 'Personal info' && (
                    <>
                    <Typography variant="h4" gutterBottom>Personal info</Typography>
                    <Divider />
                    <Typography variant="h6" gutterBottom style={{ marginTop: '1rem' }}>Profile</Typography>
                    {isEditing ? (
                    <Box 
                        display="flex" 
                        flexDirection="column" 
                        sx={{ 
                            padding: '1rem', 
                            backgroundColor: '#f5f5f5', 
                            borderRadius: '5px',
                            marginTop: '1rem'
                        }}
                    >
                        <TextField 
                            value={name} 
                            onChange={e => setName(e.target.value)} 
                            sx={{ 
                                marginBottom: '1rem' 
                            }} 
                        />
                        <TextField 
                            value={phone_number} 
                            onChange={e => setPhone_number(e.target.value)} 
                            sx={{ 
                                marginBottom: '1rem' 
                            }} 
                        />
                        <Button 
                            variant="outlined" 
                            onClick={handleSaveClick} 
                            sx={{ 
                                alignSelf: 'flex-start' 
                            }}
                        >
                            Save
                        </Button>
                    </Box>
                    ) : (
                      <>
                        <Typography variant="body1">{name}</Typography>
                        <Typography variant="body1">{phone_number}</Typography>
                        <Typography variant="body1">{Cookies.get('kerberos')}</Typography>
                        <EditButton variant="outlined" onClick={handleEditClick}>Edit Profile</EditButton>
                      </>
                    )}
                    <Typography variant="h6" gutterBottom style={{ marginTop: '1rem' }}>School</Typography>
                    <Typography variant="body1">{school}</Typography>
                  </>
                    )}
                    {selectedSection === 'Security' && (
                    <>
                        <Typography variant="h4" gutterBottom>Security</Typography>
                        <Divider />
                        <Typography variant="h6" gutterBottom style={{ marginTop: '1rem' }}>Password</Typography>
                        {isChangingPassword ? (
                        <Box 
                            display="flex" 
                            flexDirection="column" 
                            sx={{ 
                            padding: '1rem', 
                            backgroundColor: '#f5f5f5f5', 
                            borderRadius: '5px',
                            marginTop: '1rem'
                            }}
                        >
                            <TextField 
                            type="password"
                            value={oldPassword} 
                            onChange={e => setOldPassword(e.target.value)} 
                            sx={{ 
                                marginBottom: '1rem' 
                            }} 
                            placeholder="Old Password"
                            />
                            <TextField 
                            type="password"
                            value={newPassword} 
                            onChange={e => setNewPassword(e.target.value)} 
                            sx={{ 
                                marginBottom: '1rem' 
                            }} 
                            placeholder="New Password"
                            />
                            <Button 
                            variant="outlined" 
                            onClick={handleSaveNewPasswordClick} 
                            sx={{ 
                                alignSelf: 'flex-start' 
                            }}
                            >
                            Save New Password
                            </Button>
                        </Box>
                        ) : (
                        <EditButton variant="outlined" onClick={handleChangePasswordClick}>Change Password</EditButton>
                        )}
                    </>
                    )}
                </Grid>
            </Grid>
        </Root>}
        </>
    );
};

export default StudentProfile;